<template>
  <div>
    <van-nav-bar title="精选">
      <div slot="left" @click="$router.push({name:'citySelect'})">
        <van-icon name="location-o" />
        {{geoLocationCity}}
      </div>
      <van-icon name="search" slot="right" @click="$router.push({name:'search'})" />
    </van-nav-bar>
    <div class="list" @scroll="handleScroll" ref="list" :class="{'center':list.length === 0}">
      <router-link :to="`/product/${item.id}`" class="item" v-for="(item, index) in list" :key="`product-${item.id}`">
        <img v-lazy="item.cover">
        <h3>{{item.name}}</h3>
        <div class="price clearfix">
          <span class="price-l">￥</span>
          <span class="cprice">{{item.price}}</span>
          <span class="price-r">起</span>
        </div>
      </router-link>
      <van-loading v-if="isLoading" color="#1989fa" />
    </div>
    <div class="toTop" @click="toTop" v-show="listScrollTop > 200">
      <van-icon name="upgrade" />
    </div>
    <tabbar />
  </div>
</template>

<script>
  import tabbar from './tabbar.vue'
  export default {
    components: {
      tabbar
    },
    data() {
      return {
        isLoading: false,
        listScrollTop:0
      }
    },
    computed: {
      geoLocationCity() {
        return this.$store.geoLocationCity
      },
      page() {
        return this.$store.productData.page
      },
      total() {
        return this.$store.productData.total
      },
      list() {
        return this.$store.productData.list
      }
    },
    methods: {
      toTop(){
        this.$refs.list.scrollTop = 0
      },
      handleScroll() {
        this.listScrollTop = this.$refs.list.scrollTop
        if (this.page == this.total) {
          return
        }
        if (this.isLoading) {
          return
        }
        if (this.$refs.list.clientHeight + this.$refs.list.scrollTop === this.$refs.list.scrollHeight) {
          this.isLoading = true
          this.$store.productData.page++
          this.getData()
        }
      },
      getData() {
        let params = {
          page: this.page,
          size: 10
        }
        this.$axios.get(`/product/pagination`, {
          params
        }).then(res => {
          if (res.errcode == 0) {
            let data = res.data
            this.$store.productData.total = data.totalPages
            this.$store.productData.list = this.$store.productData.list.concat(data.data)
            this.$store.skeletonVisible = false
          }
          this.isLoading = false
        })
      }
    },
    mounted() {
      if (this.$store.productData.list.length === 0) {
        this.isLoading = true
        this.getData()
      } else {
        this.$refs.list.scrollTop = this.$store.productData.scrollTop
      }
    },
    beforeRouteLeave(to, from, next) {
      this.$store.productData.scrollTop = this.$refs.list.scrollTop
      next()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .list {
    background-color: white;
  }

  .van-loading {
    text-align: center;
    padding-bottom: 20px;
  }

  .my-tab {
    height: 10vw;
    width: 100vw;
    display: flex;
    align-items: center;
    overflow-y: hidden;
    flex-wrap: nowrap;
  }

  .my-tab a {
    display: inline-block;
    padding: 0 20vw;
    text-align: center;
    line-height: 10vw;
    white-space: nowrap;
  }

  .van-swipe {
    margin-bottom: 10px;
  }

  .van-swipe-item img {
    width: 100%;
    height: 40vw;
  }

  .van-icon-search {
    font-size: 5vw;
  }
</style>


